.audiovisual {
  position: relative;
  overflow: hidden;
}
.audiovisual .bgImage {
  position: absolute;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  filter: brightness(50%) blur(4px);
}
.audiovisual .visualiser {
  position: absolute;
  height: 100%;
  width: 100%;
  bottom: 0;
}
.audiovisual .progressContainer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 0.5%;
}
.audiovisual .progressContainer .progress {
  height: 100%;
  transition: width 0.2s linear;
}
.audiovisual .waveZero {
  position: absolute;
  left: 0;
  right: 0;
}
.audiovisual .fade.fadeIn {
  opacity: 0;
}
.audiovisual .fade.fadeIn.fadeInActive {
  opacity: 1;
  transition: opacity 0.5s ease;
}
.audiovisual .fade.fadeOut {
  opacity: 1;
}
.audiovisual .fade.fadeOut.fadeOutActive {
  opacity: 0;
  transition: opacity 0.5s ease;
}
.audiovisual .fadeOutScale {
  visibility: hidden;
}
.audiovisual .fadeOutScale.fadeOutScaleTransition {
  visibility: visible;
  opacity: 1;
  transform: scale(1, 1);
}
.audiovisual .fadeOutScale.fadeOutScaleTransition.fadeOutScaleTransitionActive {
  opacity: 0;
  transform: scale(1.5, 1.5);
  transition: all 0.5s ease;
}
.audiovisual .play {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -5vh;
  margin-left: -4.333vh;
  width: 0;
  height: 0;
  border-top: 5vh solid transparent;
  border-bottom: 5vh solid transparent;
  border-left: 8.666vh solid;
}
.audiovisual .pause {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -5vh;
  margin-left: -5vh;
  width: 2.5vh;
  height: 10vh;
  border-left: 2.5vh solid;
  border-right: 2.5vh solid;
}
